import { configureStore, createSlice } from "@reduxjs/toolkit";

//  
let users = createSlice({
    name: "users",
    initialState: [
        { id: 0, name: "", age: "35", joined: "2015", foot: "", profile: "186/95" },
        { id: 1, name: "", age: "36", joined: "2015", foot: "", profile: "183/88" },
        { id: 2, name: "", age: "29", joined: "2015", foot: "", profile: "162/60" }
    ],
    reducers: {
        addPlayer(state, action) {
            console.log(action);
            const type = action.payload.type;
            const userData = action.payload.userData;
            let todo = [...state];

            switch (type) {
                case "ADD_USERS":
                    todo.push(userData);

                    break;
            }
            return todo;
        }
    }
});
export let { addPlayer } = users.actions;

export default users;
